import React, { useState } from 'react';
import { Button, Form } from 'react-bootstrap';
import InputField from '../../../shared/components/form/InputField';
import uniformApi from 'api/uniformApi';
import { ToastWrapper } from 'utils';
import { useForm } from 'react-hook-form';
import styled from 'styled-components';

function RegistrationForm() {
  const userInfo = JSON.parse(localStorage.getItem('user-info') || '{}');
  const [loading, setLoading] = useState(false);

  const { handleSubmit, control, reset, setError } = useForm({
    mode: 'onSubmit',
    reValidateMode: 'onChange',
    defaultValues: {
      name: userInfo?.name,
      tel: userInfo?.tel,
      zalo: userInfo?.zalo,
    },
    resolver: undefined,
    context: undefined,
    criteriaMode: 'firstError',
    shouldFocusError: true,
    shouldUnregister: true,
    shouldUseNativeValidation: false,
    delayError: undefined,
  });

  const onSubmit = (data) => {
    const quantity = parseInt(data?.quantity);

    if (!Number.isInteger(quantity) || Number(quantity) <= 0) {
      return setError('quantity', {
        type: 'custom',
        message: 'S lng phi ln hn 0',
        shouldFocus: true
      });
    }

    setLoading(true);
    uniformApi
      .register(data)
      .then((res) => {
        console.log(res);
        reset({
          name: '',
          tel: '',
          zalo: '',
          note: '',
          quantity: '',
        });
        ToastWrapper(
          'ng k thnh cng. Chng mnh s lin h vi bn trong thi gian sm nht!',
          'success'
        );
        setLoading(false);
      })
      .catch((error) => {
        ToastWrapper(
          'ng k tht bi. Vui lng lin h di ng/zalo 0877876877  c h tr!',
          'error'
        );
        setLoading(false);
      });
  };

  return (
    <Styles>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <InputField label={'Tn ca bn'} control={control} name='name' />
        <InputField label={'in thoi lin h'} control={control} name='tel' />
        <InputField label={'Zalo'} control={control} name='zalo' />
        <InputField
          label={'S lng d kin'}
          control={control}
          name='quantity'
        />
        <InputField
          label={'Ghi ch/Gp '}
          placeholder='Nhp ghi ch hoc gp  ca bn'
          as='textarea'
          rows={3}
          control={control}
          name='note'
          rules={{ required: false }}
        />
        {loading ? (
          <Button variant='primary' className='submit-btn' type='button'>
            Vui lng ch...
          </Button>
        ) : (
          <Button variant='primary' className='submit-btn' type='submit'>
            ng k ngay
          </Button>
        )}
      </Form>
    </Styles>
  );
}

export default RegistrationForm;

const Styles = styled.div`
  .submit-btn {
    background-color: var(--primary);
    border-color: var(--primary);
    width: 100%;
    margin: 1rem 0 5rem;
  }

  .files-stack {
  }
`;
