import React from 'react';
import { useState } from 'react';
import { Button, Modal } from 'react-bootstrap';

function ConfirmModal({ onConfirm, hideModal, data }) {

  return (
    <Modal show onHide={hideModal}>
      <Modal.Header closeButton>
        <Modal.Title>{data?.title || 'Xc nhn'}</Modal.Title>
      </Modal.Header>
      <Modal.Body>{data?.body || 'Bn c mun tip tc thc hin hnh ng ny?'}</Modal.Body>
      <Modal.Footer>
        <Button variant='outline-secondary' onClick={hideModal}>
          Hy b
        </Button>
        <Button variant='danger' onClick={onConfirm}>
          Xc nhn
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

export default ConfirmModal;
